"use client"

import Link from "next/link";
import { ReactElement } from "react";
import { RiTwitterXFill, RiYoutubeFill, RiMetaFill, RiGoogleFill } from "react-icons/ri";
import { footerData } from "@/public/assets/data/footer-links";

interface Media {
  logo: ReactElement;
  to: string;
}

interface FooterItem {
  name: string;
  to: string;
}

export interface FooterSection {
  heading: string;
  subheadings: FooterItem[];
}

const Footer = () => {
    const socialMediaLinks: Media[] = [
    {
      logo: <RiMetaFill className="w-[19.5px] h-[19.43px] text-[var(--richblack-400)]" />,
      to: "https://www.facebook.com/studyliv0/"
    },
    {
      logo: <RiGoogleFill className="w-[19.5px] h-[19.43px] text-[var(--richblack-400)]" />,
      to: "https://www.studyliv.com/"
    },
    {
      logo: <RiTwitterXFill className="w-[19.5px] h-[19.43px] text-[var(--richblack-400)]" />,
      to: "https://x.com/studyliv0"
    },
    {
      logo: <RiYoutubeFill className="w-[19.5px] h-[19.43px] text-[var(--richblack-400)]" />,
      to: "https://www.youtube.com/studyliv0"
    },
  ];

    return (
        <div className="w-full h-[728px] border-t-[1px] border-solid border-t-[var(--richblack-600)] py-[52px] px-[120px] flex flex-col gap-8
        bg-[var(--richblack-800)]">

            <div className="w-[83.4%] h-[73%] flex gap-[52px]">
                {footerleftData.map((sectionGroup, groupIndex) => (
                    <div key={groupIndex} className={`flex gap-3 w-full ${groupIndex === 0 ? '' : 'flex-col'}`}>
                        {sectionGroup.map((section, sectionIndex) => (
                            <div key={sectionIndex} className={`w-full ${groupIndex === footerleftData.length-1 && sectionIndex === 0 ? 'h-[200px]' : 'h-full'} gap-3 flex flex-col`}>
                                <p className="w-full h-[24px] text-base font-semibold leading-6 text-[var(--richblack-100)]">
                                    {section.heading}
                                </p>

                                <div className={`${groupIndex === footerleftData.length-1 && sectionIndex === 0 ? 'h-[84px]' : 'h-[29.3%]'} w-full flex flex-col gap-2`}>
                                    {section.subheadings.map((item, itemIndex) => (
                                        <Link
                                            href={item.to}
                                            className="w-full h-[26.9%] font-normal text-sm leading-5.5 text-[var(--richblack-400)]"
                                            key={itemIndex}
                                        >
                                            {item.name}
                                        </Link>
                                    ))}
                                </div>
                                {groupIndex === 0 && sectionIndex === 0 && (
                                    <div className="w-[75.9%] h-[51.2%] gap-3 flex">
                                        {socialMediaLinks.map((element, index) => (
                                            <Link href={element.to} key={index} className="w-6 h-6">
                                                {element.logo}
                                            </Link>
                                        ))}
                                    </div>
                                )}
                            </div>
                        ))}
                    </div>
                ))}

                <div className="w-[44.9%] -rotate-90 h-0 border border-solid border-[var(--richblack-700)]"/>

                <div className="w-[45.7%] h-full gap-3 flex">
                  {
                    footerRightData.map((section,index) => (
                      <div key={index} className="w-full h-full flex flex-col gap-3">
                        <p className="w-full h-[24px] text-base font-semibold leading-6 text-[var(--richblack-100)]">
                          {section.heading}
                        </p>
                        <div className="h-fit w-full flex flex-col gap-2">
                          {section.subheadings.map((item, itemIndex) => (
                            <Link
                            href={item.to}
                            className="w-full h-[26.9%] font-normal text-sm leading-5.5 text-[var(--richblack-400)]"
                            key={itemIndex}
                            >
                              {item.name}
                            </Link>
                          ))}
                        </div>
                      </div>
                    ))
                  }
                </div>
            </div>

            <div className="w-[83.4%] h-0 border-solid border border-[var(--richblack-700)]"/>

            <div className="w-[83.4%] h-[3%] flex gap-3">

            </div>
        </div>
    );
}

export default Footer;